import React, { useEffect, useState } from 'react'
import Find_Image from './Find_IMage'
import { connect } from 'react-redux'
import { R_FU_FIND_IMAGE_CHANGE , get_image,get_current_image} from '../../redux/FindImage/FindImage.redux'
import { cauntPage } from '../../helps'


let pages

const Find_Image_Container=(props)=>{
    const [ArrCount,setArrCount]=useState([])
    const page = +props.match.params.page

    useEffect(()=>{
        console.log(1);
        props.get_image(props.image.page,props.image.text,props.image.per_page)
        .then(e=>{
            pages = Math.ceil(e.total / 12)
                setArrCount(cauntPage(page,pages))
        })
    },[props.image.text,props.image.per_page,props.image.page])
    useEffect(()=>{
        props.R_FU_FIND_IMAGE_CHANGE(props.image.text,page,props.image.per_page)
    },[page])

    return <Find_Image {...props} ArrCount={ArrCount}/>
}


const dataL = [ 'search']
const mapStateToProps = state => {

    return {
        image:state.image,
        text: state.i18n.text,
        lang: state.i18n.lang
    }
}



export default connect(mapStateToProps,{R_FU_FIND_IMAGE_CHANGE,get_image,get_current_image})(Find_Image_Container)