import React from "react";
import 'chart.js/auto';
import { Chart, Line, Bar, Radar, Pie, Doughnut } from 'react-chartjs-2';
import { TimeForChart } from "../../helps";
import { useState } from "react";


const dataUNIX = {
  min5: 300,
  hours: 3600,
  day: 86400,
  month: 2592000,
  year: 31536000
}

const allChartType = [
  {
    name: 'line',
    component: Line,
  },
  {
    name: 'radar',
    component: Radar,
  },
  {
    name: 'bar',
    component: Bar,
  },
  {
    name: 'doughnut',
    component: Doughnut,
  },
  {
    name: 'pie',
    component: Pie,
  }
]


const Chart_coin = (props) => {
  const [Component, setComponent] = useState(Line)
  const limitTime = dataUNIX.day
  const [input_data, setInput] = useState({ first: '', last: '' })
  // const day = props.dataYear.lastYear
  let dayChange = props.dataYear.lastYear;


  function type_change(e) {
    // console.log(e.target.value);
    setComponent(allChartType[+e.target.value].component)
    // setComponent( allChartType.find(el=>el.name==e.target.value).component)

  }





  const state = {
    labels: props.history.map(el=>{
      const date=new Date(el[0])
      // console.log(date);
      return date.getDate()+'-'+date.getMonth()+'-'+date.getFullYear()
    }),
    datasets: [
      {
        label: 'Price',
        fill: false,
        lineTension: 0.5,
        backgroundColor: 'rgba(75,192,192,1)',
        borderColor: 'rgba(0,0,0,1)',
        borderWidth: 2,
        data: props.history.map(el => el[1])
      }
    ]
  }
  // console.log(props);
  // console.log(input_data.first);
  const regxp = /([0-9]){0,}/gi
  // input_data.first.match(regxp)
  // console.log(input_data.first.match(regxp));
  return <div>
    <div>
      <input type='date' onChange={(el) => { setInput({ first: el.target.value, last: input_data.last }) }} />
      <input type='date' onChange={(el) => { setInput({ first: input_data.first, last: el.target.value }) }} />
      <button onClick={(el) => {
        let first = new Date(input_data.first.match(regxp)[0], input_data.first.match(regxp)[2], input_data.first.match(regxp)[4])
        let last = new Date(input_data.last.match(regxp)[0], input_data.last.match(regxp)[2], input_data.last.match(regxp)[4])
        let start = new Date(1970, 1, 1)
        
        first-=start
        last-=start
        first = first.toString().slice(0, first.toString().length-3)
        last = last.toString().slice(0, last.toString().length-3)
        props.setdataYear({lastYear:first,currentYear:last})
      }}>
        Use settings
      </button>
    </div>
    <select onChange={type_change}>
      {allChartType.map((type, ind) => <option key={"chart" + type.name} value={ind}>{type.name[0].toUpperCase() + type.name.slice(1)}</option>)}


    </select>
    <Component data={state} />

  </div>



}




export default Chart_coin