import { Color } from "../../Local/Local"

const D = {
    CHANGE_AVATAR: 'CHANGE_AVATAR',
    CHANGE_COLOR: 'CHANGE_COLOR'
    // SET_AVATAR:'SET_AVATAR'
}


const data = {}


export const SettingsRedux = (state = data, action) => {
    switch (action.type) {
        case D.CHANGE_AVATAR:
            return { ...state, avatar: action.avatar }
        case D.CHANGE_COLOR:
            return { ...state, color: action.color }
        default: return state
    }
}


export const R_FU_SETTINGS_CHANGE_AVATAR = (avatar) => ({ type: D.CHANGE_AVATAR, avatar })
export const R_FU_SETTINGS_CHANGE_COLOR = (color) => { Color.set(color); return{ type: D.CHANGE_COLOR, color }}


export const set_avatar = ( file,api) => dispatch=> {
   
    api.profile.put_photo(file)
        .then(e => {
            console.log(e);
        })
}