import React from "react";
import C from '../FindCoin/FindCoin.module.css'


const AnswerCont = (props) => {
    console.log(props);
    return <div className={C.result_cont}>
        {
            props.result.map(el => <Curent_search_coin key={el.id + 'Ans'} el={el} />)
        }
    </div>
}



const Curent_search_coin = (props) => {
    return <div>
        <div className={C.result_find}>
            <p>id:{props.el.id}</p>
            <p>name:{props.el.name}</p>
            <p>symbol:{props.el.symbol}</p>
        </div>

    </div>
}



export default AnswerCont