import React, { useEffect, useState } from "react";
import './Error.css'
import C from './Error.module.css'
import Modal from 'react-modal'

const Error_api = ({ textError_mod, setError_mod, isError_mod }) => {


  const customStyles = {
    overlay: {
      position: 'absolute',
    right: 0,
    top: '10px',
    height: '70px',
    width: '20%',
    'marginLeft': '80%',
    'marginTop': '40px',
    border: '1px solid black',


    },
  };






  return <div>
    <Modal className='C.error'
      isOpen={isError_mod}
      ariaHideApp={false}
      style={customStyles}
      shouldCloseOnEsc={
        true}
    >
      <div className={C.button_close}>
        <button  onClick={() => {
          setError_mod(false)
        }}>
          X
        </button>
        
      </div>
<p>{textError_mod}</p>



    </Modal>
  </div>
}



export default Error_api