import React, { memo, useEffect, useMemo, useState } from "react";
import { connect } from "react-redux";
import Mesager from "./Mesager";
import { R_FU_USERS_FIND_CHANGE } from "../../redux/Users/Users_find.redux";
import { compose } from "redux";
import { get_dialogs } from "../../redux/Mesager/Dialogs.redux";
import { add_text } from "../../redux/i18.redux/i18.redux";


const Mesager_container=(props)=>{
   
    const [is_new,setNew]=useState('')

    useEffect(() => {
        props.add_text(dataL)
    }, [props.lang])
    
    useEffect(()=>{
        props.get_dialogs(props.api)
    },[props.api,is_new])

    return <Mesager match={props.match} {...props} setNew={setNew}  />
}

const dataL = ["friends", 'no_message','chat_chose']
const mapStateToProps = state => {
    console.log(state);
    return {
        api: state.api,
        users: state.users,
        users_find: state.users_find,
        messages:state.messages,
        user:state.user,
        dialogs:state.dialogs,
        text: state.i18n.text,
        lang: state.i18n.lang
    }
}



export default compose(
     connect(mapStateToProps, { R_FU_USERS_FIND_CHANGE,get_dialogs,add_text}),
     
)(Mesager_container)
