import React,{memo} from 'react'
import Todo_current from "./Todo_current";
import C from './Css/All_todo.module.css'

const All_todo =props=>{
    console.log(props);

    


    return <div className='all_todo'>

        <div className={C.LeftBox}>
           { props.TODO
           .filter(d=>!d.isChecked)
           .map(el=><Todo_current key={el._id}  el={el}{...props }/>)}

        </div>
        <div className={C.RightBox}>
        {
            props.TODO.filter(d=>d.isChecked).map(el=><Todo_current key={el._id} {...props} el={el}/>)
        }

        </div>
    </div>
}




export default memo(All_todo)