const D={
    GET_MESAGES:'GET_MESAGES',
}

const messages={

}

export const MesagerRedux=(state=messages,action)=>{
    switch(action.type){
        case D.GET_MESAGES:
            return action.messages
        default:
         return state
    }
}

const R_FU_MESSAGER_GET_MESSAGES=(messages)=>({type:D.GET_MESAGES,messages})

export const send_messege=(api,id,text)=>dispatch=>{
    api.dialog.send_mesege(id,text)
    .then(e=>{
        api.dialog.get_mesages(id)
        .then(el=>{
            dispatch(R_FU_MESSAGER_GET_MESSAGES(el))
        })
    })
}
export const get_mesagges=(api,id)=>dispatch=>{
    api.dialog.get_mesages(id)
    .then(el=>{
        // console.log(el);
        dispatch(R_FU_MESSAGER_GET_MESSAGES(el))
    })
}