import React from "react";
import C from "./Coin.module.css"

const Description=(props)=>{
    console.log(props);

    const regxp=/<a href[^]*<\/a>/gi
   
    
    return <div className={C.description}>
        {regxp.test(props.description[props.len])?props.description[props.len].replace(regxp,''):props.description[props.len]}
    </div>
}




export default Description