import React, { useState } from "react";
import C from '../FindImage/find_image.module.css';
import { connect } from "react-redux";
import { get_image,R_FU_FIND_IMAGE_CHANGE } from "../../redux/FindImage/FindImage.redux";
import Image from "../FindImage/Image";

const Interface_image=(props)=>{

    const [images,setImages]=useState(props.image.allIMG)
    console.log(props);
    const ScrollAddUsers = e =>{
       const  offsetHeight=e.target.offsetHeight
       const ScrollHeight = e.target.scrollHeight;
       const ScrollTop = e.target.scrollTop
        

       if(ScrollHeight - 80 < offsetHeight + ScrollTop ){
         
        props.R_FU_FIND_IMAGE_CHANGE(props.image.text,props.image.page+1,props.image.per_page)
          props.get_image(props.image.page,props.image.text,props.image.per_page)
          .then(e=>{
              console.log(e);
            setImages([...images,...e.hits])
          })
       }
    }
    console.log(images);
    return <div className={C.UsersMobile}  onScroll={ScrollAddUsers}>
        {images.map(el =>
            <Image data={el} key={el.id} />
        )}
    </div>
}

const mapStateToProps = state => {

    return {
        image:state.image,
        text: state.i18n.text,
        lang: state.i18n.lang
    }
}



export default connect(mapStateToProps,{get_image,R_FU_FIND_IMAGE_CHANGE})(Interface_image)