const D={   
    CHANGE_DIALOGS:'CHANGE_DIALOGS'
}

const dialogs=[]

export const DialogsRedux=(state=dialogs,action)=>{
    switch(action.type){
        case D.CHANGE_DIALOGS:
            return action.dialogs
        default:
            return state
    }
}

export const R_FU_DIALOGS_CHANGE=(dialogs)=>({type:D.CHANGE_DIALOGS,dialogs})



export const get_dialogs=(api)=>dispatch=>{
    api.dialog.get_dialogs()
    .then((e)=>{
         console.log(e);
        dispatch(R_FU_DIALOGS_CHANGE(e))
    })
}       