const D={
    CHANGE_USERS:'CHANGE_USERS'
}

const users =  []




export const UsersRedux = (state = users, action) => {
    switch (action.type) {
        case D.CHANGE_USERS:
            return action.users
        default:
            return state
    }
}

const R_FU_USERS_CHANGE_USERS=(users)=>({type:D.CHANGE_USERS,users})

export const get_users = (api,params) => dispatch => {
  return  api.API_social.get_users(params)
        .then(e => {
            // console.log(1);
            // pages = Math.ceil(e.totalCount / count)
            // setArrCount(cauntPage(page))
            dispatch(R_FU_USERS_CHANGE_USERS(e.items))
            return e
        })
}