import React, { useEffect,useState } from 'react'
import './CoinList.css'
import Crypto_price from './Crypto_pirce'
import Spiner from '../Spiner/Spiner'
import { API } from '../../api'
import { NavLink } from 'react-router-dom'
const Curent_coin_info= props=>{
    const [obj_crypto,setObj]=useState(null)
    const [spiner,setSpiner] = useState(true)

    useEffect(()=>{

        setSpiner(true)
        
        API.coin(props.currentCoin)
        .then(setObj)
        .finally(setSpiner(false))
        
    },[props.currentCoin])
      

        if(spiner|| !obj_crypto) return <Spiner/>
        if(!props.currentCoin) return <div>Error </div>
        return <div className='crypto_inf_cont'>
            <h3>
            <NavLink  to={`Coin/${obj_crypto.id}`}>

                {obj_crypto.name}
            </NavLink>
            </h3>
            <div className='main_inf'>
                <img src={obj_crypto.image.small} />
                <div className='symbol_cont'>
                    <p>
                        symbol
                    </p>
                    <p>
                        {obj_crypto.symbol}
                    </p>
                </div>
            </div>
            <Crypto_price price={obj_crypto.market_data.current_price}/>
            
        </div>
    }

export default Curent_coin_info