import axios from 'axios'



const api_key='20648109-aff5f53cd54a5f40fa07937d6'


const defoltURL ='https://pixabay.com/api/?image_type=photo&orientation=horizontal'
const instons = axios.create({
    baseURL : defoltURL
})    



const Erorr = response=>response.status==200 ? response.data :  console.error(response)
export const API_IMAGE = {
    get_image:(page,text,per_page)=>instons.get(`&q=${text}&page=${page}&per_page=${per_page}&key=${api_key}`).then(Erorr)
}
