import React, { useEffect, useState } from "react";
import { API } from "../../api";
import Spiner from "../Spiner/Spiner";
import C from "./Coin.module.css"
import Description from "./Description";
import Chart_coin from "./Chart_coin";
import { changeHistory } from "../../redux/Coin/Coin.redux";
import { connect } from "react-redux";

const retDATA = ()=>{
    let date = Date.now()
        let curent_date=date.toString().slice(0, date.toString().length-3)
        let start_date=curent_date-31536000
    return {
        curent_date,
        start_date
    }
}





const Coin=(props)=>{
    const [dataYear,setdataYear]=useState({
    lastYear : retDATA().start_date,
    currentYear : retDATA().curent_date
})

    let [data,setData]=useState(null)
    // let [history,setHistory]=useState([])
    useEffect(()=>{
        
        API.coin(props.match.params.id)
        .then(setData)
        
    },[])
    console.log(props);
        useEffect(()=>{
        // }
        const  params = {
            date: dataYear.lastYear,
            name : props.match.params.id,
            price : 'usd',
            curent_date : dataYear.currentYear
        }
            
        // console.log(start_date);
           props.changeHistory(params)
            console.log(props);
        },[dataYear])  
    if(!data || !props.history.data)return <Spiner />
    return <div>
      
        <div className={C.inf_cont}>
            <img src={data.image.large}/>
            <div className={C.description_cont}>
                <h2>
                    {data.name}
                </h2>
                <div>
                    <Description description={data.description} len={'en'}/>
                </div>
            </div>
            
        </div>
      <div>
          

                <Chart_coin  dataYear={dataYear} setdataYear={setdataYear} history={props.history.data.prices}/>
            </div>
    </div>
}




const mapStateToProps = state => {
    
    return {
        allList : state.coin.allList,
        history : state.coin.history,
    }
}


export default connect(mapStateToProps,{changeHistory})(Coin)