const defoltURL ='https://api.coingecko.com/api/v3/'
const instons = axios.create({
    baseURL : defoltURL
})    



const Erorr = response=>response.status==200 ? response.data :  console.error(response)
export const API = {
    list: ()=> instons.get('coins/list').then(Erorr),
    coin: (name)=>instons.get(`coins/${name}`).then(Erorr),
    history:({date,name,price,curent_date})=>instons.get(`coins/${name}/market_chart/range?vs_currency=${price}&from=${date}&to=${curent_date}`)
}



