import React, { createRef, useContext, useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom'
import C from './header.module.css'
import './header.css'
import Modal from 'react-modal'
import UserContext from '../../userContext'
import LogIn_container from '../User/LogIn_container'
import { Lang, User_Local } from '../../Local/Local'
import Error from '../User/Error'
import { connect } from 'react-redux'
import { R_FU_USER_LOGAUT_USER } from '../../redux/User/User.redux'
import Settings from '../Settings/Settings'
import { change_lang } from '../../redux/i18.redux/i18.redux'
import { add_text } from '../../redux/i18.redux/i18.redux'


const Header = (props) => {
  const [emailErr, setEmail] = useState('')
  const [currentComponent, setComponent] = useState(null)
  const [error_list, setErrorList] = useState([])
  const [isSettings, setSettings] = useState(false)
  const [lang, setLang] = useState('')
  const customStyles = {
    overlay: {
      position: 'fixed',
      right: '0px',
      height: '99vh',
      width: '50%',
      // marginLeft: '75%',
      border: '1px solid black',
      zIndex: '50',
      padding: 0


    },
    content: {
      width: '100%',
      padding: 0,
      inset: 0,
      background: 'none',
      border: 'none'
    }
  };

  useEffect(() => {
    props.add_text(dataL)
  }, [props.lang])

  const setErrorFun = (data) => {
    setErrorList([...error_list, data])
  }

  const lang_change = (e) => {
    console.log(e);
    props.change_lang(e.target.value)
    setLang(e.target.value)
  }

  const ref_hcont = createRef()
  const ref_head = createRef()

  return <header ref={ref_head}>
    <div className='Hcont' ref={ref_hcont}>
      {props.user.fullName &&
        <NavLink to={'/Home/' + props.user?.userId}>Home</NavLink>}
      <NavLink to="/Todo">Todo</NavLink>
      <NavLink to="/CoinList">CoinList</NavLink>
      <NavLink to="/FindCoin">FindCoin</NavLink>
      <NavLink to={"/Users/" + 1}>Users</NavLink>
      <NavLink to={"/FindImage/"+1}>Find Image</NavLink>
      
      {props.user.fullName &&
        <NavLink to={"/Mesager"}>Mesager</NavLink>
      }
      <NavLink to="/Settings">Settings</NavLink>
      {
        props.user.fullName

          ? <div className={C.person}>
            <p> {props.user?.fullName} </p>
            <button onClick={() => {
              User_Local.remove()
              props.R_FU_USER_LOGAUT_USER()
            }}>{props.text?.log_out}</button>
          </div>
          : <div>

            <button onClick={() => {
              setComponent('LogIn')

            }}>{props.text?.log_in}</button>
          </div>
      }
      <div>
        <select onChange={lang_change} value={props.lang}>
          <option>uk</option>
          <option>ru</option>
          <option>en</option>

        </select>
      </div>

      <Modal
        style={customStyles}
        isOpen={isSettings}
        ariaHideApp={false}
      >
        <Settings />
      </Modal>

      <Modal
        isOpen={currentComponent === 'LogIn'}
        ariaHideApp={false}
      >
        <LogIn_container
          emailErr={emailErr}
          setEmail={setEmail}
          setComponent={setComponent}
          setErrorList={{ setErrorFun, setErrorList }}
          error_list={error_list}
          currentComponent={currentComponent} />
      </Modal>

      <Modal
        isOpen={currentComponent === 'Error'}
        ariaHideApp={false}

      >
        <Error error_list={error_list} setComponent={setComponent} />
      </Modal>
    </div>
    <div className={C.settings}>
      <button className={C.span_opt} onClick={() => {
        ref_head.current.classList.toggle('active_head')
        return ref_hcont.current.classList.toggle('active')

      }}>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </button>
    </div>
  </header>
}
const dataL=['log_out','log_in']
const mapStateToProps = state => {
  return {
    // lang:state.lang,
    text: state.i18n.text,
    lang: state.i18n.lang,
    user: state.user
  }
}


export default connect(mapStateToProps, { R_FU_USER_LOGAUT_USER, change_lang,add_text })(Header)