

import { generate as id } from 'shortid'
const D = {
    DELETE: 'DELETE',
    CHECK: 'CHECK',
    ADD: 'ADD'
}
const todo = [
    {
        _id: id(),
        text: 'JS',
        isChecked: false
    },
    {
        _id: id(),
        text: 'CSS',
        isChecked: true
    },
    {
        _id: id(),
        text: 'HTML',
        isChecked: true
    },
]


const TodoRedux = (state = todo, action) => {
    switch (action.type) {
        case D.DELETE:
            return state.filter(el => el._id != action.id)
        case D.CHECK:
            return state.map(el => {
                if (el._id == action.id) {
                    el.isChecked = !el.isChecked
                }
                return el
            })
        case D.ADD:

            const obj = {
                _id: id(),
                text: action.text,
                isChecked: false
            }
            return [...state,obj]
        default: return state
    }
}


export default TodoRedux



export const R_FU_TODO_DELETE = id => ({ type: D.DELETE, id })
export const R_FU_TODO_CHECK = id => ({ type: D.CHECK, id })
export const R_FU_TODO_ADD = text => ({ type: D.ADD, text })