import React, { useContext, useEffect, useState } from "react";
import C from "./Users.module.css"
import img from '../Users/img_def.jpg'
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { follow, unfollow } from "../../redux/User_follow_unfollw/User_follow_unfollow.redux";
const User = (props) => {
    const context = props.user
    const api = props.api
    const [followed, setFollowed] = useState(props.followed)

    useEffect(() => {
        if (props.response.answer) {
            setFollowed(props.response.follow)
        }
    }, [props.response])

    const folow_click = () => {
        props.follow(api, props.id)
    }

    const unfollow_click = () => {
        props.unfollow(api, props.id)
    }


    return <div className={C.user} key={props.id}>
        {props.photos.small ? <img className={C.img} src={props.photos.small} /> : <img className={C.img} src={img} />}
        <div className={C.main_inf}>
            <Link to={'/Home/' + props.id}>{props.name}</Link>
            {context?.email && (followed ? <button onClick={unfollow_click}>{props.text?.unsubscribe}</button> : <button onClick={folow_click}>{props.text?.subscribe}</button>)}

        </div>

    </div>
}

const mapStateToProps = state => {

    return {
        api: state.api,
        user: state.user,
        response: state.follow_unfollow

    }
}



export default connect(mapStateToProps, { follow, unfollow })(User)