import React from "react";
import C from "./User.module.css"


const Error = (props) => {
    return <div>
        <div>
            {
                props.error_list.map(e => {
                    return <div>
                        <h3>
                            Error {e.name}
                        </h3>
                        {e.data}
                    </div>
                })
            }
        </div>
        <button className={C.close_button} onClick={() => props.setComponent(null)}>
            X
        </button>
    </div>
}



export default Error