import React, { useEffect } from 'react';
import Input from './Input';
import All_todo from './All_todo';
import { connect } from 'react-redux';
import { R_FU_TODO_ADD, R_FU_TODO_DELETE, R_FU_TODO_CHECK } from '../../redux/Todo/Todo.redux'
import { add_text } from '../../redux/i18.redux/i18.redux';




const dataL = ["add"]

const Todo_Container = (props) => {

    useEffect(() => {
        props.add_text(dataL)
    }, [props.lang])


    return <main>
        <Input add={props.R_FU_TODO_ADD} text={props.text} />
        <All_todo TODO={props.todo} fun={{ deletTODO: props.R_FU_TODO_DELETE }} checkedTODO={props.R_FU_TODO_CHECK} />
    </main>
}



const mapStateToProps = state => {
    console.log(state.lang);

    return {
        todo: state.todo,
        text: state.i18n.text,
        lang: state.i18n.lang
        // i18 : state.i18
    }
}

export default connect(mapStateToProps, { R_FU_TODO_CHECK, R_FU_TODO_DELETE, R_FU_TODO_ADD, add_text })(Todo_Container)