import { apiToken } from '../../api_social'


const api = apiToken('')

const T = {
    CHANGE_TOKEN: 'CHANGE_TOKEN',
    GET_ME:'GET_ME',
    GET_PROFILE:'GET_PROFILE',
}
// export const data_user=[]

const Api = (state = api, action) => {
    switch (action.type) {
        case T.CHANGE_TOKEN:
            return apiToken(action.token)
        // case T.GET_ME:
            // console.log(action.data);
            // data_user.push(action.data)
            // console.log(data_user);
            // return state
        default: return state
    }
}



export const R_FUN_API_CHANGE_TOKEN = token =>({type:T.CHANGE_TOKEN, token})
