import React, { useState } from "react";


const allOption = ['id','name','symbol']


const InputCont=(props)=>{


    const [value,setValue]=useState(0)
    const [opt,setOPt]=useState('id')

    const search_click=()=>{
        props.setCoin_to_search({value,opt})
    }

    const input_change=(e)=>{
            setValue(e.target.value)     
    }
    const select_change=(e)=>{
        console.log(e.target.value);
        setOPt(e.target.value)

    }






    return <div>
        <input onChange={input_change}/>
        <select onChange={select_change}>
            {allOption.map(n=><option key={n+'InputCoin'}>{n}</option>)}
        </select>
        <button onClick={search_click}>
            {props.text?.search}
        </button>
    </div>
}





export default InputCont