const D = {
    FOLLOW: 'FOLLOW',
    UNFOLLOW: 'UNFOLLOW',
}
const data = {
    answer : null,
    follow : null

}

export const User_follow_unfollowRedux = (state=data, action) => {
    switch (action.type) {
        case D.FOLLOW:
            return { 
                answer : action.data === 200 ? true : null,
                follow : true
            }
             
        case D.UNFOLLOW:
            return { 
                answer : action.data === 200 ? true : null,
                follow : false
            }
        default:
            return state
    }
}
const R_FU_USERS_FOLLOW=(data)=>({type:D.FOLLOW,data})
const R_FU_USERS_UNFOLLOW=(data)=>({type:D.UNFOLLOW,data})

export const follow=(api,id)=>dispatch=>{
    api.follow.follow(id).then(el=>{
        console.log(el);
        dispatch(R_FU_USERS_FOLLOW(el?.resultCode))

    })
}
export const unfollow=(api,id)=>dispatch=>{
    api.follow.unfollow(id).then(el=>{
        dispatch(R_FU_USERS_UNFOLLOW(el?.resultCode))

    })
}