import React, { useContext, useEffect, useState } from "react";
import LogIn from "./LogIn";
import { apiToken, auth, profile, setToken } from "../../api_social";
import UserContext from "../../userContext";
import { User_Local } from "../../Local/Local";
import { connect } from "react-redux";
import { R_FUN_API_CHANGE_TOKEN } from "../../redux/Api.redux/Api.redux";
import {R_FU_USER_AUTIFICATION} from '../../redux/User/User.redux'
import { get_link } from "../../redux/Security/Security.redux";

const LogIn_container=(props)=>{
    const context = useContext(UserContext)
    const setApi=context.setApi
    const api=props.api
    useEffect(()=>{
        props.get_link(api)
    },[])
    function LogIn_click(email,password,remember) {
        api.auth.login_post(email,password,remember).then(el=>{

            api.profile.porfile_get(el?.data?.userId)
            .then(e=>{
                if(!e)return
                props.R_FU_USER_AUTIFICATION({...e,email})
                console.log(el);
                props.R_FUN_API_CHANGE_TOKEN(el.token)
                

                if(!remember)return
                User_Local.set(el.token)
            })
            
            
        })
        
    }
    
    
setTimeout(() => {
    console.log(1)
}, 0);
let p = new Promise((resolve,reject)=>{
    console.log(2)
    resolve()
})
p.then(()=>{
    console.log(3)
})
console.log(4);


    return <LogIn {...props} LogIn_click={LogIn_click}/>

}
const dataL=['log_in','log_out']
const mapStateToProps = state => {
    
    return {
        api : state.api,
        security:state.security
    }
}


export default connect(mapStateToProps,{R_FUN_API_CHANGE_TOKEN,R_FU_USER_AUTIFICATION,get_link})(LogIn_container)