import React, { useEffect, useRef, useState } from "react";
import { connect } from "react-redux";
import User from "../Users/User";
import C from '../Users/Users.module.css'
import { get_users } from "../../redux/Users/Users.redux";
import { R_FU_USERS_FIND_CHANGE } from "../../redux/Users/Users_find.redux";

const InterfaceUsers=(props)=>{
   const [users,setUsers]  = useState(props.users)
   const [isSpinner,setSpinner]= useState(false)
    // useEffect(()=>{
    //     setUsers([...users,...props.users])
    // },[props.users])

    const ScrollAddUsers = e =>{
        if(isSpinner) return;
       const  offsetHeight=e.target.offsetHeight
       const ScrollHeight = e.target.scrollHeight;
       const ScrollTop = e.target.scrollTop


       if(ScrollHeight - 80 < offsetHeight + ScrollTop ){
         setSpinner(true)
        //  console.log(props.users_find);
         const params={
             ...props.users_find,page:props.users_find.page+1
         }
         props.R_FU_USERS_FIND_CHANGE(params)
          props.get_users(props.api,params)
          .then(e=>{
              console.log(e);
              setUsers([...users,...e.items])
              setSpinner(false)
          })
       }
    //    const H = document.body.clientHeight - 88;
 
    //    console.log(e.target.getBoundingClientRect());
    //    console.log(e.target.style.height);
    //    if()
    }
    // console.log(props);
    return <div className={C.UsersMobile} onScroll={ScrollAddUsers}>



         {users.map(el =>
            <User key={el.id} {...el} />
        )}
        {isSpinner && 'Loading...'}
    </div>
}


const mapStateToProps = state => {

    return {
        api: state.api,
        users: state.users,
        users_find: state.users_find
    }
}



export default connect(mapStateToProps,{get_users,R_FU_USERS_FIND_CHANGE})(InterfaceUsers)