import React, { useEffect,useState } from 'react'
import axios from 'axios'
import './CoinList.css'

const Crypto_price=(props)=>{
    const [allPrice,setAllPrice]=useState([])
    const [current_price,setCurrent_price]=useState({})

    // console.log(props);
    useEffect(()=>{
        //  const first_fun=()=>{
        const allP=[]
        for (const i in props.price) {
            if(i==='usd') continue
            allP.push({
                key:i,
                price:props.price[i]
            })
            

        }
        setAllPrice(allP)




        set_defoult_price(JSON.stringify({price:props.price['usd'],key:'usd'}))
    // }
    // first_fun()
    },[props.price])
    const set_defoult_price=(e)=>{
        setCurrent_price(JSON.parse(e))
    }
   const price_change =(e)=>{

   
   setCurrent_price(JSON.parse(e.target.value))

   }

    const p =  props.price['usd']
   return <div className='price'>
       
       <select  onChange={price_change}>
        <option key={'usd'}  value={JSON.stringify({price:p,key:'usd'})}>usd</option>)
        {allPrice.map(el=><option key={el.key}  value={JSON.stringify({price:el.price,key:el.key})}>{el.key}</option>)}
       </select>
   <p>
        {current_price.price} {current_price.key}
   </p>
</div>
 
}

// [<otionval value={name} />]
// [{
//     key ; 'asd',
//     price : '1231233'
// }]

export default Crypto_price