import { User_Local } from "../../Local/Local"
import { get_Me, R_FUN_API_CHANGE_TOKEN, R_FUN_API_GET_ME } from "../Api.redux/Api.redux"

const D = {
    AUTIFICATION: 'AUTIFICATION',
    LOGAUT_USER:'LOGAUT_USER',
    CHANGE_STATUS:'CHANGE_STATUS'

}
const user = {
    
}


const UserRedux = (state = user, action) => {
    switch (action.type) {
        case D.AUTIFICATION:
            return action.user
        case D.LOGAUT_USER :
            return {}
        // case D.CHANGE_STATUS:
        //     console.log(state);
        //     return {...state,aboutMe:action.status}
        default: return state
    }
}

export default UserRedux

export const R_FU_USER_AUTIFICATION = (user) => ({ type: D.AUTIFICATION, user })
export const R_FU_USER_LOGAUT_USER = () => ({ type: D.LOGAUT_USER })
// export const R_FU_USER_CHANGE_STATUS =(status)=>({type:D.CHANGE_STATUS,status})

export const auto_login = () => dsipatch => {
    User_Local.get()
        .then((el) => {
            if (!el) return

            dsipatch(R_FUN_API_CHANGE_TOKEN(el))
        })

}
export const get_User = (api) => dsipatch => {
    if ('' === api.token) return
    api.auth.get_me()
    .then(el => {
            api.profile.porfile_get(el.data.id)
            .then(e => {
                dsipatch(R_FU_USER_AUTIFICATION({ ...e, email: el.data.email }))
              
                    // setContextObj({ ...e, email: el.data.email })
            })
    })
    // console.log(1);
    //   dsipatch(get_Me(user_data))
    // console.log(api);


}
