import React, { useState, useEffect } from "react";
import C from './Mesager.module.css'
import { connect } from "react-redux";
import { Link } from 'react-router-dom'
import { send_messege, get_mesagges } from "../../redux/Mesager/Mesager.redux";
import { R_FU_DIALOGS_CHANGE } from "../../redux/Mesager/Dialogs.redux";
import { get_dialogs } from "../../redux/Mesager/Dialogs.redux";

const Chat = (props) => {

    const [input, setInput] = useState('')
    const input_change = (e) => {
        setInput(e.target.value)
    }

    const send_click = (id) => {
        props.send_messege(props.api, id, input)
        setInput('')
    }

    useEffect(() => {
        if (!props.dialogs.find(el => el.id == props.match.params.id)) {
            if (!props.match.params.id) return
            props.api.dialog.start_chat(props.match.params.id)
                .then(e => {
                    setTimeout(() => { props.setNew(props.match.params.id) }, 500)
                })
        }
    }, [props.match.params.id])
    useEffect(() => {
        // console.log(!props.dialogs[0]);

        if (!props.match.params.user || !props.dialogs[0]) return
        // console.log('tt12');
        // console.log(props.dialogs);
        props.get_mesagges(props.api, props.match.params.id)

    }, [props.match.params.user, props.dialogs])
    console.log(props);

    return <div className={C.chat_container} onTouchStart={props.onTouchStart} onTouchMove={props.onTouchMove} onTouchEnd={props.onTouchEnd}>
        <div className={C.chat}>
            {props.match.params.user &&
                <div className={C.top_menu}>
                    <h3>
                        {props.match.params.user}
                    </h3>
                    <p>
                        id:{props.match.params.id}
                    </p>
                </div>
            }
            <div className={C.main_chat}>
                {

                    props.messages.totalCount ? <div>
                        {props.messages.items.map(el => {
                            if (el.senderName == props.user.fullName) {
                                return <div className={C.my_mesages} key={el.id}>
                                    {el.body}
                                </div>
                            }
                            else {
                                return <div className={C.oponenet_mesages} key={el.id}>
                                    {el.body}
                                </div>
                            }
                        })

                        }
                    </div> : <div>{props.text?.no_message}</div>
                }
            </div>
        </div>
        {props.match.params.user ? <div className={C.bottom_menu}>
            <input type='text' onChange={input_change} value={input} />
            <button onClick={() => { send_click(props.match.params.id) }}>Send</button>
        </div>
            : <div className={C.bottom_menu}>
                {props.text?.chat_chose}
            </div>
        }
    </div>
}

const mapStateToProps = state => {

    return {
        messages: state.messages,
        api: state.api,
        users: state.users,
        user: state.user,
        dialogs: state.dialogs,
        lang: state.lang.component.Mesager
    }
}



export default connect(mapStateToProps, { send_messege, get_mesagges, R_FU_DIALOGS_CHANGE, get_dialogs })(Chat)