import React from "react";
import C from '../FindImage/find_image.module.css'
import ImageForHOC from "./ImageForHOC";
import { Link } from "react-router-dom";
import Image from "./Image";
const Image_list=(props)=>{
    console.log(props);
    return <div>
        <div className={C.image_container}>
            {props.image.allIMG&& props.image?.allIMG.map(el=>{
                return <Image data={el} key={el.id} {...props}/>
            })}
        </div>
        <div className={C.pages}>
            {props.ArrCount.map((el, i) => <div className={C.page} key={'page_' + i}>{el ? el === props.image.page ? <Link to={'/FindImage/' + el} className={C.current_page}>{el}</Link> : <Link to={'/FindImage/' + el}>{el}</Link> : "..."}</div>)}
        </div>
        
    </div> 


}


export default Image_list