import { memo, useState } from "react"
import React from 'react'

const checkMemo =(prevP,nextP)=>(prevP.el.isChecked === nextP.el.isChecked&&prevP.el.text === nextP.el.text)  

const Todo_current=(props)=>{
// console.log(props);
const [check, setCheck]=useState(props.el.isChecked)
const [value, setvalue]=useState(props.el.text)
const [inp, setInp]=useState(false)
const checked=(id)=>{
    props.checkedTODO(id)

    setCheck(!check)
    
}
const dbl_inp=(e)=>{
    setInp(!inp)
    console.log(e.target);
}
const imputchange=(e)=>{
    setvalue(e.target.value)
}
    return <div className={check.toString()}>
        <input type="checkbox" checked={check} onChange={()=>checked(props.el._id)}/>
        {
        inp
        ?<input type="text" value={value} onChange={imputchange} onBlur={dbl_inp}/>
        :<p onDoubleClick={dbl_inp}>{value}</p>
        }
        

        <button onClick={()=>props.fun.deletTODO(props.el._id)}>X</button>
    </div>
}


// export default Todo_current
export default memo(Todo_current,checkMemo)