import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import AnswerCont from './AnswerCont';
import InputCont from './InputCont';
import { add_text } from '../../redux/i18.redux/i18.redux';


const FindCoin = (props) => {


    const [coin_to_search, setCoin_to_search] = useState(null)
    const [result, setResult] = useState([])
    
    useEffect(() => {
        props.add_text(dataL)
    }, [props.lang])

    useEffect(() => {
        if (!coin_to_search) return

        setResult(
            props.allList.filter(e => e[coin_to_search.opt].includes(coin_to_search.value)
            )

        )
    }, [coin_to_search])



    return <div>

        <InputCont setCoin_to_search={setCoin_to_search} text={props.text} />
        <AnswerCont result={result} />
    </div>
}


const dataL = ['search']
// export default   SpinerHOC(FindCoin)
const mapStateToProps = state => {

    return {
        allList: state.coin.allList,
        text: state.i18n.text,
        lang: state.i18n.lang
    }
}


export default connect(mapStateToProps,{add_text})(FindCoin)