import React, { useContext, useEffect, useState } from 'react'
import { connect } from 'react-redux';
import { R_FU_SETTINGS_CHANGE_AVATAR, set_avatar, R_FU_SETTINGS_CHANGE_COLOR } from '../../redux/Settings/Settings.redux';
import C from '../Settings/settings.module.css'
import { get_User } from '../../redux/User/User.redux';

const Settings = (props) => {

    useEffect(() => {
        // if (!props.api) return
        props.get_User(props.api)
        // console.log(props.user);
    }, [props.settings.avatar])

        // console.log(props.user);

    const photo_change = (e) => {
        const form = new FormData()
        form.append('image', e.target.files[0])
        props.R_FU_SETTINGS_CHANGE_AVATAR(form);
    }

    const change_click = () => {
        // console.log(props.settings.avatar);
        if (props.settings) {

            props.set_avatar(props.settings.avatar, props.api)
        }
    }
    const color_input = (e) => {
        props.R_FU_SETTINGS_CHANGE_COLOR(e.target.value)
        // console.log(e.target.value);
    }


    return <div >
        {props.user?.photos?.large && <div className={C.change_img_cont}>
            <img src={props.user?.photos?.large} />
            <div className={C.img_input}>
                <input type='file' className='photo' onChange={photo_change} />
                <button onClick={change_click}>Change</button>
            </div>
           
        </div> }
        <div className={C.color_cont}>
            <p>
                Change theme
            </p>
            <input onInput={color_input} value={props.settings.color ? props.settings.color : '#ffffff'} type='color' />
        </div>
    </div>
}


const mapStateToProps = state => {

    return {
        // home:state.home,
        user: state.user,
        api: state.api,
        settings: state.settings
    }
}


export default connect(mapStateToProps, { R_FU_SETTINGS_CHANGE_AVATAR, set_avatar, R_FU_SETTINGS_CHANGE_COLOR,get_User })(Settings)