import React, { useContext, useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link, Redirect } from "react-router-dom";
import UserContext from "../../userContext";
import Users from "./Users";
import { get_users } from "../../redux/Users/Users.redux";
import { R_FU_USERS_FIND_CHANGE } from "../../redux/Users/Users_find.redux";
import { add_text } from "../../redux/i18.redux/i18.redux";
import { cauntPage } from "../../helps";

const Users_container = (props) => {

    const [count, setCount] = useState(10);
    const [isRedirect, setRedirect] = useState(false)
    const page = +props.match.params.page
    const [ArrCount, setArrCount] = useState([])
    const api = useContext(UserContext).api


    const changeReirect = (count) => {
        setCount(count)
        setRedirect(true)
    }


    useEffect(() => {
        const params = {
            page,
            count,
            term: '',
            friend: null
        }
        props.R_FU_USERS_FIND_CHANGE(params)

    }, [count, page])

    useEffect(() => {
        isRedirect && setRedirect(false)
    }, [isRedirect])

    useEffect(() => {
        props.add_text(dataL)
    }, [props.lang])

    useEffect(() => {

        props.get_users(props.api, props.users_find)


            .then(e => {
                pages = Math.ceil(e.totalCount / count)
                setArrCount(cauntPage(page,pages))
            })
    }, [props.users_find])


    if (!page || isRedirect) {
        return <Redirect to={'/Users/1'} />
    }

    if (page <= 0 || page > pages) {
        return <div>
            <h1>
                {props.text.error_users_undefinded}
            </h1>
            <Link to={'/Users/1'}> {props.text.to_page_1} </Link>
        </div>
    }
    else {
        return <div>
            <Users page={page} setCount={changeReirect} count={count} ArrCount={ArrCount} users={props.users} text={props.text} />
        </div>
    }

}


const dataL = ['unsubscribe', 'subscribe', 'search', 'start_chat','to_page_1','error_users_undefinded']
const mapStateToProps = state => {

    return {
        api: state.api,
        users: state.users,
        users_find: state.users_find,
        text: state.i18n.text,
        lang: state.i18n.lang
    }
}



export default connect(mapStateToProps, { get_users, R_FU_USERS_FIND_CHANGE,add_text })(Users_container)



let pages
