import React from "react";
import C from './Users.module.css'
import { Link } from "react-router-dom";
import User from "./User";

const Users_list=(props)=>{
    return <>
        <div className={C.users_container} >
        {props.users.map(el =>
            <User key={el.id} {...el} text={props.text} />
        )}
    </div>
    <div className={C.pages}>
        {props.ArrCount.map((el, i) => <div className={C.page} key={'page_' + i}>{el ? el === props.page ? <Link to={'/Users/' + el} className={C.current_page}>{el}</Link> : <Link to={'/Users/' + el}>{el}</Link> : "..."}</div>)}
    </div>
    </>
}


export default Users_list