import { get_Me, R_FUN_API_CHANGE_TOKEN, R_FUN_API_GET_ME } from "../Api.redux/Api.redux"

const D = {
    GET_PROFILE: 'GET_PROFILE',
    CHANGE_STATUS: 'CHANGE_STATUS'

}
const user = {

}


const HomeRedux = (state = user, action) => {
    switch (action.type) {
        case D.GET_PROFILE:
            console.log(action.user);
            return action.user
        case D.CHANGE_STATUS:
            console.log(state);
            return { ...state, status: action.status }
        default: return state
    }
}

export default HomeRedux

export const R_FU_USER_GET_PROFILE = (user) => ({ type: D.GET_PROFILE, user })
export const R_FU_USER_CHANGE_STATUS = (status) => ({ type: D.CHANGE_STATUS, status })


export const get_Home = (api, id) => dsipatch => {

    api.profile.porfile_get(id)
        .then(e => {
            dsipatch(R_FU_USER_GET_PROFILE(e))
            dsipatch(get_status(api,id))

        })




}
export const get_status = (api, id) => dsipatch => {
    api.profile.get_status(id)
                .then(e => {
                    dsipatch(R_FU_USER_CHANGE_STATUS(e))
                })
}

export const set_status = (status,api)=> dsipatch => {
    api.profile.status_put(status)
    .then((e)=>{
        console.log(e);
        if(e.resultCode===0){
            dsipatch(R_FU_USER_CHANGE_STATUS(status))
        }
    })
}