import React, { useState } from 'react'

import './CoinList.css'
import Curent_coin_info from './Curent_coin_info'
import Option from './Option'
import SpinerHOC from '../../HOC/SpinerHOC'

import { connect } from 'react-redux'
import { getAllList,R_FU_COIN_CHANGE_CURENT } from '../../redux/Coin/Coin.redux'

const CoinList = props =>{
    
const crypto_inf=(e)=>{
    props.R_FU_COIN_CHANGE_CURENT(e.target.value)
}

    return <div className='all_cont'><select onChange={crypto_inf}>
        {props.allList.map(data => <Option key={data.id} {...data} />)}
    

    </select>
    
    {props.current.map(CoinCur =><Curent_coin_info  key={CoinCur+'cur'} currentCoin={CoinCur} />)}
        </div>
}
const mapStateToProps = state => {
    
        return {
            current : state.coin.current,
            allList : state.coin.allList,
        }
    }
    
    
export default connect(mapStateToProps,{getAllList,R_FU_COIN_CHANGE_CURENT})(CoinList)




 