import React, { useEffect} from "react"
import App from "./App"
import { connect } from "react-redux";
import { getAllList } from "./redux/Coin/Coin.redux";
import { auto_login, get_User } from "./redux/User/User.redux";
import { get_lang } from "./redux/Language/Language.redux";
import { useTranslation } from "react-i18next";
import { addT_I18n } from "./redux/i18.redux/i18.redux"; 
import Settings from "./Component/Settings/Settings";
import Spiner from "./Component/Spiner/Spiner";
import { Color } from "./Local/Local";
import { R_FU_SETTINGS_CHANGE_COLOR } from "./redux/Settings/Settings.redux";
// import { I18nextProvider } from 'react-i18next';
// import i18n from './redux/i18_lang'

const AppContainer = (props) => {
    const {t, i18n} =useTranslation()
   
    // console.log(t('friends'));
    // console.log(i18n);

    useEffect(() => {
        if (!props.api) return
        props.get_User(props.api)
    }, [props.api])
    useEffect(() => {
        props.auto_login()
        props.getAllList()
        props.get_lang()
        Color.get().then(props.R_FU_SETTINGS_CHANGE_COLOR)
        // i18n.changeLanguage('en')
        
    }, [])
    useEffect(()=>{
        props.addT_I18n(t,i18n)
        console.error(i18n.language);
    },[props.lang])
    
    return props.i18n.t?<App color={props.color} />:<Spiner/>

}




const mapStateToProps = state => {
    console.log(state);
    return {
        coin: state.coin,
        api: state.api,
        user: state.user,
        lang:state.lang,
        i18n:state.i18n,
        lang:state.i18n.lang,
        settings:state.settings,
        color : state.settings.color
    }
}


export default connect(mapStateToProps, { getAllList, auto_login, get_User,get_lang,addT_I18n,R_FU_SETTINGS_CHANGE_COLOR })(AppContainer)