import React, { useContext } from "react"
import localforage from "localforage"
import UserContext from "../userContext"
import { UserError } from "../Erors/Errors"
const back = localforage.createInstance('back')
const User = localforage.createInstance('User')
const lang= localforage.createInstance('Lang')
const color = localforage.createInstance('Color')

export const User_Local = {




  get: () => User.getItem('User'),
  get_token: () => User.getItem('User'),

  set: (token) => {
    
      
          User.setItem('User',token)
          
         
        
      




  },
  remove: () => User.removeItem('User'),
  auto_LogIn:(token)=>{
    return Back.get()
      .then(data=>{
          const findUser = data.find(user=>{
              return (user.token===token)
          })
          if(findUser){
            return {...findUser, password : ''}
          }
      })
  },
  LogIn: function ({ email, password }, ErrorF, setContext) {
    Back.get()
      .then(data => {
        const findUser = data.find(user => {
          return (user.email === email && user.password === password)
        })

        if (findUser) {
          let date = Date.now()
            let curent_date=date.toString().slice(0, date.toString().length-3)
          setContext(findUser)
          this.set(findUser)
          User.setItem('User', {token:findUser.token,date: +curent_date})
        } else {
          if (data.find(user => { return user.email == email })) {
        

            ErrorF.setErrorList({ name: 'password', data: 'incorect password' })
          }
          else {
        
            ErrorF.setErrorList(UserError.notFind(ErrorF))
            console.log(email);
            ErrorF.setEmail(email)
          }
        }

      })
}

}

export const Back = {
  get: () => back.getItem('BackUser'),
  set: function (user) {
    this.get().then((allUser) => {

      allUser
        ? back.setItem('BackUser', [...allUser, user])
        : back.setItem('BackUser', [user])

    })
  }
}

export const Lang={
  get:()=>lang.getItem('Lang'),
  set:(lan)=>{lang.setItem('Lang',lan)}
}



export const Color={
  get:()=>lang.getItem('Color'),
  set:(color)=>{lang.setItem('Color',color)}
}