import React, { useState } from 'react'
import C from '../FindImage/find_image.module.css'
import Image from './Image';
import { Link } from 'react-router-dom';
import D from 'lodash.debounce'
import ImageForHOC from './ImageForHOC';
import Modal from 'react-modal'

const Find_Image=(props)=>{

    console.log(props);
    const [input, setInput] = useState('')
    const [is_large,setLarge]=useState(false)
    const [large_src,setSrc]=useState('')

    const change_curent=()=>{
        // console.log(1);
        props.get_current_image(input)
    }
    // const avatar_click=()=>{
    //     const current=props.image.allIMG.filter(e=>{
    //         if(e.largeImageURL==large_src){
    //             return e
    //         }
    //     })[0]
    //     console.log(current);
    // }

    const style={
        overlay: {
            display:'flex',
            width:'100%',
            'alignItems':'center',
            'justifyContent':'center'

          }
        
    }

    const inputchange = (el) => {
        setInput(el.target.value) 
        console.log('2');
        D(()=>console.log(11),600)    
    }

    const prev_image=()=>{
        props.image.allIMG.map((e,i)=>{
            if(e.largeImageURL==large_src){
                if(i==0){
                    setSrc(props.image.allIMG[11].largeImageURL)
                }
                else{
                    setSrc(props.image.allIMG[i-1].largeImageURL)
                }
            }
        })
    }

    const next_image=()=>{
        props.image.allIMG.map((e,i)=>{
            if(e.largeImageURL==large_src){
                if(i==11){
                    setSrc(props.image.allIMG[0].largeImageURL)
                }
                else{
                    setSrc(props.image.allIMG[i+1].largeImageURL)
                }
                
            }
        })
    }

    const search_click = () => {
        props.R_FU_FIND_IMAGE_CHANGE(input,1,12)
        setInput('')
    }


    return <div>
       
        <div className={C.search_cont} >
            <input type='text' className={C.input_search} value={input} onChange={inputchange} />
            
            <button onClick={search_click}>
                {props.text?.search}
            </button>
            {input.trim()&& <div className={C.pre_result}>
            {props.image.currentAllImg&& props.image?.currentAllImg.map(el=>{
                return <Image data={el} key={el.id}/>
            })}
            </div>}
        </div> 
      <ImageForHOC {...props} setLarge={setLarge} is_large={is_large} setSrc={setSrc}/>
      <Modal
       style={style}
       className={C.large_image_modal}
        isOpen={is_large}
        ariaHideApp={false}
      >


          <img className={C.large_image} src={large_src} onDoubleClick={()=>{setLarge(false)}}/>
          <button className={C.prev}   onClick={prev_image}> {'<'} </button><button className={C.next} onClick={next_image}> {'>'} </button>
          {/* <button className={C.avatar} onClick={avatar_click}>Set as avatar</button> */}
      </Modal>
    </div>
}

export default Find_Image