import React, { useEffect, useState } from "react";
import C from "./Users.module.css"
import { R_FU_USERS_FIND_CHANGE } from "../../redux/Users/Users_find.redux";
import { connect } from "react-redux";
import { get_users } from "../../redux/Users/Users.redux";
import UsersForHoc from "./UsersForHoc";
const selDate = [10, 20, 50, 75, 100]

const Users = (props) => {
    const [friend_req, setFriend] = useState(false)
    const [isFriend, setisFriend] = useState(null)
    const [input, setInput] = useState('')

    const Swtich_cahnge = (el) => {
        setFriend(el.target.checked);
        !el.target.checked ? setisFriend(null) : setisFriend(false)
    }

    const isFriend_change = (el) => {
        setisFriend(el.target.checked)

    }

    const inputchange = (el) => {
        setInput(el.target.value)
    }

    const search_click = () => {
        const params = {
            page: props.page,
            count: props.count,
            term: input,
            friend: isFriend
        }
        props.R_FU_USERS_FIND_CHANGE(params)
        props.get_users(props.api, props.users_find)
    }

    return <div>
        <select className={C.select_count} value={props.count} onChange={(el) => { props.setCount(+el.target.value) }}>
            {selDate.map(c => <option key={c + 'sel'}>{c}</option>)}
        </select>


        <div className={C.search_cont} >
            <input type='text' className={C.input_search} value={input} onChange={inputchange} />
            <div className={C.switch}>
                <label className={C.form_switch} >
                    <input type="checkbox" onChange={Swtich_cahnge} />
                    <i></i>

                </label>
                {
                    friend_req && <div>
                        <label className={C.form_switch} >
                            <input type="checkbox" onChange={isFriend_change} />
                            <i></i>

                        </label>
                    </div>
                }
            </div>
            <button onClick={search_click}>
                {props.text?.search}
            </button>
        </div>
        <UsersForHoc users={props.users} text={props.text} ArrCount={props.ArrCount} page={props.page} />

    </div>
}



const mapStateToProps = state => {

    return {
        api: state.api,
        users: state.users,
        users_find: state.users_find
    }
}



export default connect(mapStateToProps, { get_users, R_FU_USERS_FIND_CHANGE })(Users)