import React, { useContext, useEffect, useState } from "react";
import C from "./User.module.css"
import Validator from 'validatorjs';
import Modal from "react-modal";
import { User_Local, Back } from "../../Local/Local";
import ReCAPTCHA from "react-google-recaptcha";
import Error from "./Error";
import InputCurrent from "./InputCurrent";

// https://azzaza.github.io/Reac_project-1/

const form = {
    email: 'yasko.zakhar@gmail.com',
    password: 'yzkuk171',
    // captcha:true
}
const form2={
    email: 'zakhar.yasko2.0@gmail.com',
    password: 'yzkuk171',
    // captcha:''
}
const rules = {
    email: 'required|email',
    password: 'required',
    // captcha:'required'
}

const LogIn = (props) => {

    useEffect(() => {
        props.setErrorList.setErrorList([])

    }, [props.currentComponent])



    const [formV, setFormV] = useState(form)
    const [checkbox, setCheckbox] = useState(false)
    // const [captcha,setCaptcha]=useState('')
    // console.log(formV);
    // const change_captch=e=>setCaptcha(e.target.value)
    const change = e => setFormV({ ...formV, [e.target.placeholder]: e.target.value })

    function send_click() {

        let validation = new Validator(formV, rules);
        if (validation.fails()) {
            erorrCreat(validation.errors.all())
            
            
        } else {
           
            props.setComponent(null)

            props.LogIn_click(formV.email,formV.password,checkbox)
          

        }

    }
    
    const erorrCreat = (error) => {
        const eror = []
        for (const i in error) {

            eror.push({
                name: i,
                data: error[i]
            })
        }
        props.setErrorList.setErrorList(eror)
    }



    return <div>
        <div className={C.form}>
            <InputCurrent name="email" change={change} error_list={props.error_list} setComponent={props.setComponent} formV={formV} />
            <InputCurrent name="password" change={change} error_list={props.error_list} formV={formV} />
            <input type='checkbox' value={checkbox} onChange={() => {
                setCheckbox(!checkbox)
            }} />
            <div>
                <button onClick={()=>{
                setFormV(form)
            }}>
                1
            </button>
            <button onClick={()=>{
                setFormV(form2)
            }}>
                2
            </button>
            </div>
            <div>
                <ReCAPTCHA
                style={{ display: "inline-block" }}
                theme="dark"
                sitekey="6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI"
                // ref={this._reCaptchaRef}
                // sitekey={TEST_SITE_KEY}
                // onChange={this.handleChange}
                // asyncScriptOnLoad={this.asyncScriptOnLoad}
                />
                {/* <img src={props.security} />
                <input type='text' value={formV.captcha} placeholder='captcha' onChange={change}/> */}
                {/* {/* <button>Send</button> */}
            </div>
            
            <button onClick={send_click}>
                Log In
            </button>

        </div>



        <button className={C.close_button} onClick={() => {
            props.setComponent(null)
        }}>
            X
        </button>



    </div>
}


export default LogIn




