import React, { useState } from "react";
import Todo_Container from './Component/Input/Todo_Container'
import CoinList from './Component/CoinList/CoinList';
import Error_404 from "./Erors/Error_404";
import Home_container from "./Component/Home/Home_container";
import Header from './Component/Header/Header'
import FindCoin from "./Component/FindCoin/FindCoin";
import Coin from "./Component/Coin/Coin";
import Users_container from "./Component/Users/Users_container";
import Mesager_container from "./Component/Mesager/Mesager_container";
import Error_api from "./Erors/Error_api";
import Find_Image_Container from "./Component/FindImage/Find_Image_container";
import Settings from "./Component/Settings/Settings";
import './all.css'
import {
  Switch,
  Route,

} from "react-router-dom";
import './App.css';
// import { useTranslation } from "react-i18next";


export const ErrorObj = {}


let isAddFun = true
 

function App(props) {
  // const { t, i18n } = useTranslation()
  // console.log(t);
  // console.log(i18n);
const [isError_mod,setError_mod]=useState(false)
const [textError_mod,setTextError_mod]=useState('')
const openError = (text='')=>{
  setError_mod(true)
  setTextError_mod(text)
  setTimeout(()=>{
    setError_mod(false)
  },5000)
  
}

if(isAddFun){ErrorObj.open = openError; isAddFun=false;}
// console.log(props.color);
  
  
  
  return (
    <div className="App">
    <Header/>
    <div className="conComponent" style={{backgroundColor:props.color ? props.color : 'white'}}>
    
    <Switch>
    <Route path="/Todo" component={Todo_Container} />
    <Route path="/CoinList"  component={CoinList}/>
    <Route path="/FindCoin" component={FindCoin}/>
    <Route path="/Coin/:id?" component={Coin}/>
    <Route path="/Home/:User?" component={Home_container}/>
    <Route path="/Users/:page?" component={Users_container}/>
    <Route path="/Mesager/:user?/:id?" component={Mesager_container}/>
    <Route path="/FindImage/:page?" component={Find_Image_Container}/>
    <Route path="/Settings" component={Settings}/>
    <Route path='/' component={Error_404}/>
     </Switch>
    </div>
      

      <Error_api ErrorObj={ErrorObj} textError_mod={textError_mod} isError_mod={isError_mod} setError_mod={setError_mod}/>
     
    </div>
  );
}

export default App;
