import axios from 'axios'
import Error_404 from './Erors/Error_404'
import {ErrorObj} from './App'



const my_token = 'ae2db1dc-021a-4bfe-86d6-4fa9aec4c6c6'
const my_token2 = '1b4fa9c1-cf69-4001-8a91-d3935c5d06ce'

const Erorr = response => response.status == 200 ? response.data :console.error(response)
const ErorrUser = response => response.status == 200 ? response.status :console.error(response)

export const apiToken = (token) => {


    const defoltURL = 'https://social-network.samuraijs.com/api/1.0/'
    const instons2 = axios.create({
        baseURL: defoltURL,
        withCredentials: true,
        headers: {
            "API-KEY": token
        }
    })
    const security={
       get_captcha_url:()=>{
           return instons2.get(`/security/get-captcha-url`)
           .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
             
            })
       }

    }
    const dialog={
        start_chat:(id)=>{
            return instons2.put(`dialogs/${id}`)
            .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
             
            })
        },
        send_mesege:(id,text)=>{
            return instons2.post(`dialogs/${id}/messages`,{body:text})
            .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
             
            })
        },
        get_mesages:(id,page=1,count=20)=>{
            return instons2.get(`dialogs/${id}/messages/?page=${page}&count=${count}`)
            .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
             
            })
        },
        get_dialogs:()=>{
            return instons2.get(`dialogs`)
            .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
             
            })
        }
    }
    const auth = {
        login_post: (email, password, remember) => {
            return instons2.post(`auth/login/?email=${email}&password=${password}&rememberMe=${remember}`).then(Erorr).then(e => {
                console.log(e);
                if(email=='yasko.zakhar@gmail.com'){
                    e.token = my_token
                }
                if(email=='zakhar.yasko2.0@gmail.com'){
                    e.token = my_token2
                }
                return e
            })
            .catch(e=>{
                console.log(e);
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
             
            })
        },

        get_me: () => {
          
                 return instons2.get('auth/me', {
                headers: {
                    "API-KEY": token

                }
            })
            .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
            })
            
            

        }
    }
    const API_social = {
        get_users: (params) => instons2.get('users/?page=' + params.page+`&count=${params.count}`+`&term=${params.term}`+`&friend=${params.friend}`).then(Erorr)

    }
    const profile = {
        porfile_get: (id) => {
            return instons2.get(`profile/${id}`)
            .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
            })
        },
        status_put: (status)=>{
            return instons2.put(`profile/status`,{status:status})
            .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
            })
        },
        get_status: (id)=>{
            return instons2.get(`profile/status/${id}`)
            .then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
            })
        },
        put_photo:(file)=>{
            return instons2.post(`profile/photo`,file,{
                headers:{
                    'Content-Type':'multipart/from-data'
                }
            }).
            then(Erorr)
            .catch(e=>{
                let r =  JSON.stringify(e);
                r = JSON.parse(r)
                ErrorObj.open(r.message)
            })
        }
    }
    const follow = {
        follow: (id) => instons2.post(`follow/${id}`).then(ErorrUser).catch(e=>{
            let r =  JSON.stringify(e);
            r = JSON.parse(r)
            ErrorObj.open(r.message)
        }),
        unfollow: (id) => instons2.delete(`follow/${id}`).then(ErorrUser).catch(e=>{
            let r =  JSON.stringify(e);
            r = JSON.parse(r)
            ErrorObj.open(r.message)
        })
    }

    return {
        token,
        follow,
        auth,
        API_social,
        profile,
        dialog,
        security
    }
}



