import React from "react";
import C from './Mesager.module.css'
import { Link } from 'react-router-dom'

const Friend_panel = (props) => {
    console.log(props);
    return <div className={C.left_panel} ref={props.ref_men}>
        <div className={C.head}>
            <h3>{props.text?.friends}</h3>
        </div>

        <div className={C.friends}>
            {props.dialogs.map(e => {

                return <Link ey={e.id + 'link'} to={`/Mesager/${e.userName}/${e.id}`}>
                    <div key={e.id + 'id'} className={C.friend_cont} >
                        {e.userName}
                    </div>
                </Link>





            })}
        </div>
    </div>
}



export default Friend_panel