import { API_IMAGE } from "../../api_image"



const D={
    GET_IMAGE:'GET_IMAGE',
    CHANGE_STATE:'CHANGE_STATE',
    CHANGE_CURRENT_ALL_IMG:'CHANGE_CURRENT_ALL_IMG',
}

const state_image={
    text:'',
    page:1,
    per_page:12,
    allIMG:[],
    currentAllImg : []
}



export const FindImageRedux=(state=state_image,action)=>{
    switch(action.type){
        
        
        case D.CHANGE_STATE:
            return {text:action.text,page:action.page,per_page:action.per_page}
        case D.GET_IMAGE:
            return {...state,allIMG:action.allIMG.hits}
        case D.CHANGE_CURRENT_ALL_IMG : 
            return {...state,currentAllImg : action.allImg }    
        default: return state
    }
}

export const R_FU_FIND_IMAGE_CHANGE=(text,page,per_page)=>({type:D.CHANGE_STATE,text,page,per_page})
const R_FU_FIND_IMAGE_CHANGE_CURRENT=(allImg)=>({type:D.CHANGE_CURRENT_ALL_IMG,allImg}) 

const R_FU_FIND_IMAGE_GET=(allIMG)=>({type:D.GET_IMAGE,allIMG})

export const get_current_image=(text,page=1,per_page=12)=>dispatch=>{
    API_IMAGE.get_image(page,text,per_page)
    .then(e=>{
        console.log(e);
        dispatch(R_FU_FIND_IMAGE_CHANGE_CURRENT(e.hits))
    })
}

export const get_image=(page,text,per_page)=>dispatch=>{
    // console.log(23);
  return  API_IMAGE.get_image(page,text,per_page)
    .then(e=>{
        dispatch(R_FU_FIND_IMAGE_GET(e))
        return e
    })
}

