import React, { useState } from 'react'
import C from '../FindImage/find_image.module.css'
import Modal from 'react-modal'

const Image = props => {
console.log(props);
    
    

    const image_click=()=>{
        props.setLarge(true)
        props.setSrc(props.data.largeImageURL)
    }

    return <div className={C.image} onClick={image_click}>
        <img src={props.data.previewURL}/>
            {/* <Modal isOpen={is_open}
            ariaHideApp={false}
            style={style}
            className={C.large_image_modal}
            >   

                <img className={C.large_image} src={props.data.largeImageURL} onClick={image_click}>
                </img>
            </Modal> */}
    </div>
}

export default Image