import { API } from "../../api"

const D = {
    GET_ALL_LIST:'GET_ALL_LIST',
    LOADING_TRUE:'LOADING_TRUE',
    LOADING_FALSE:'LOADING_FALSE',
    CHANGE_CURRENT_COIN:'CHANGE_CURRENT_COIN',
    CHANGE_HISTORY:'CHANGE_HISTORY'
}
const data = {
    allList: [],
    isLoading : false,
    current:[],
    history: []
}


const CoinRedux = (state = data, action)=>{
    switch(action.type){
        case D.GET_ALL_LIST: 
        return {...state,allList : action.data}
        case D.LOADING_TRUE:
            // console.log(state);
            return {...state,isLoading:true}
        case D.LOADING_FALSE:
            return {...state,isLoading:false}
        case D.CHANGE_CURRENT_COIN:
            // console.log(action);
            return {...state,current:[...state.current,action.current]}
        case D.CHANGE_HISTORY:
            return {...state,history:action.history}
        default : return state
    }
}

export default CoinRedux

 const R_FU_COIN_GET_LIST = (data) => ({ type: D.GET_ALL_LIST,data })
 const R_FU_COIN_LOADING_TRUE=()=>({type:D.LOADING_TRUE})
 const R_FU_COIN_LOADING_FALSE=()=>({type:D.LOADING_FALSE})
 const R_FU_COIN_CHANGE_HISTORY=(history)=>({type:D.CHANGE_HISTORY,history})
export const R_FU_COIN_CHANGE_CURENT=(current)=>({type:D.CHANGE_CURRENT_COIN,current})
 

export const getAllList = ()=>dsipatch => {
    dsipatch(R_FU_COIN_LOADING_FALSE())
    API.list()
    .then(data=>dsipatch(R_FU_COIN_GET_LIST(data)))
    .finally(()=>dsipatch(R_FU_COIN_LOADING_TRUE()))

}


export const changeHistory = (params)=>dsipatch =>{
     API.history(params)
     .then(data=>{
         dsipatch(R_FU_COIN_CHANGE_HISTORY(data))
     })

}