// const {t, i18n} =useTranslation()
const D={
    ADD_I18:'ADD_I18',
    ADD_TEXT:'ADD_TEXT',
    CHANGE_LANG:'CHANGE_LANG'
}

export const I18Redux=(state={},action)=>{
    switch(action.type){
        case D.CHANGE_LANG:
            state.i18n.changeLanguage(action.lang)
            return {...state,lang:action.lang}
        case D.ADD_I18:
            console.log(action);
        return {...state,t:action.t,i18n:action.i18n,lang :action.i18n.language}
        case D.ADD_TEXT : 
        console.log(state);
const t = {}
action.text.forEach(w=>{
    t[w] = state.t(w)
})

        
        return {...state, text : t }
        default:
            return state
        
    }
}

export const addT_I18n = (t,i18n)=>({type:D.ADD_I18,t,i18n})

export const add_text=(text)=>({type:D.ADD_TEXT,text})

export const change_lang=(lang)=>({type:D.CHANGE_LANG,lang})