import React from 'react'
const InputCurrent = ({name,change,error_list,setComponent,formV})=>{

const error = error_list.find(d=>d.name === name)
const clickLogIn = ()=>{
    setComponent(error.comonent)
}

    return  <div>
        <input type={name} placeholder={name} value={formV[name]} onChange={change} />


        {error && <div>
            <p>
             {error.data}
            </p>
            {error.comonent  &&  <button onClick={clickLogIn}>{error.comonent}</button> }
        </div>}
    </div>
}

export default InputCurrent