export function TimeForChart(timestamp) {
    let d = new Date(timestamp * 1000), // Convert the passed timestamp to milliseconds
    year = d.getFullYear(),
    month = +('0' + (d.getMonth() + 1)).slice(-2),  // Months are zero based. Add leading 0.
    day = +('0' + d.getDate()).slice(-2);         // Add leading 0.
    
    return {
        year,
        month,
        day,
        T : year + ':' + month + ':' + day
    };
}

const limit = 5

export const cauntPage = (page = 1,pages) => {
    if (pages <= limit) {
        const ArrCount = [1]
        if (pages === 1) {

            return ArrCount
        }
        else {
            for (let i = 2; i <= pages; i++) {
                ArrCount.push(i)
            }
            return ArrCount
        }

    }
    else {
        const Arrcount = [1]
        if (page === 1 || page === 2 || page === 3) {
            for (let i = 2; i <= limit; i++) {
                Arrcount.push(i)
            }
            Arrcount.push(null)
        }
        else if (page === pages || page === pages - 1 || page === pages - 2) {
            Arrcount.push(null)
            for (let i = pages - limit + 1; i < pages; i++) {
                Arrcount.push(i)
            }
        }
        else {
            Arrcount.push(null)
            for (let i = (page - 2); i < (page + limit - 2); i++) {
                Arrcount.push(i)
            }
            Arrcount.push(null)
        }

        Arrcount.push(pages)
        return Arrcount
    }

}
