import TodoRedux from "./Todo/Todo.redux";
import { applyMiddleware, combineReducers, createStore } from 'redux'
import thunkMiddleware from 'redux-thunk'
import CoinRedux from "./Coin/Coin.redux";
import UserRedux from "./User/User.redux";
import Api from "./Api.redux/Api.redux";
import { UsersRedux } from "./Users/Users.redux";
import { User_follow_unfollowRedux } from "./User_follow_unfollw/User_follow_unfollow.redux";
import { UsersFindRedux } from "./Users/Users_find.redux";
import HomeRedux from "./Home/Home.redux";
import { MesagerRedux } from "./Mesager/Mesager.redux";
import { DialogsRedux } from "./Mesager/Dialogs.redux";
import { LanguageRedux } from "./Language/Language.redux";
import { SecurityRedux } from "./Security/Security.redux";
import { I18Redux } from "./i18.redux/i18.redux";
import { FindImageRedux } from "./FindImage/FindImage.redux";
import { SettingsRedux } from "./Settings/Settings.redux";


const allReducers = combineReducers({
    todo: TodoRedux,
    coin: CoinRedux,
    user: UserRedux,
    api: Api,
    users: UsersRedux,
    users_find: UsersFindRedux,
    follow_unfollow: User_follow_unfollowRedux,
    home: HomeRedux,
    settings:SettingsRedux,
    messages: MesagerRedux,
    dialogs: DialogsRedux,
    i18n:I18Redux,
    lang: LanguageRedux,
    image: FindImageRedux,
    security: SecurityRedux,
    

})

const store = createStore(allReducers, applyMiddleware(thunkMiddleware))

window.store = store;


export default store