
const D={
    SET_LINK:'SET_LINK'
}


const captcha_link=''



export const SecurityRedux=(state=captcha_link,action)=>{
    switch(action.type){
        case D.SET_LINK:return action.link
        default: return state
    }
}
const R_FU_SECURITY_SET=(link)=>({type:D.SET_LINK,link})

export const get_link=(api)=>dispatch=>{
    api.security.get_captcha_url()
        .then(e=>{
            console.log(e);
            dispatch(R_FU_SECURITY_SET(e.url))
        })
}