import React from 'react'
import { useState } from "react"
import './Css/Input.css'
const Input=(props)=>{
    const [value, setvalue]=useState('')
    const save_value=(e)=>{
        setvalue(e.target.value)
        
    }
    const exp_value=()=>{
        // console.log(props);
        props.add(value)
        setvalue('')
    }
    return <header className='header'>
        <input type="text" value={value} onChange={save_value}/>
        <button onClick={exp_value}>{props?.text?.add}</button>
        </header>
}
export default Input