import React, { useContext, useEffect, useState } from "react";
import { connect } from "react-redux";
import Home from "./Home";
import { get_Home,get_status } from "../../redux/Home/Home.redux";
import { add_text } from "../../redux/i18.redux/i18.redux";

const Home_container = (props) => {
    console.log(props.home);
    const api=props.api

    useEffect(() => {
        props.add_text(dataL)
    }, [props.lang])

    useEffect(() => {
        if(!api) return
        props.get_Home(api,props.match.params.User)
    }, [api,props.match.params.User])

    return <Home data={props.home} isSpiner={props.home.fullName} id={props.match.params.User} text={props.text}/>

}

const dataL=['start_chat','contacts','no_information']
const mapStateToProps=state=>{
    return {
        api:state.api,
        home:state.home,
        text: state.i18n.text,
        lang: state.i18n.lang
    }
}




export default connect(mapStateToProps,{get_status,get_Home,add_text})(Home_container)