const D={
    CHANGE_PARAMS:'CHANGE_PARAMS'
}


const paramsFind = {
    page : 1,
    count:10,
    term : '',
    friend: null
}


export const UsersFindRedux = (state = paramsFind, action) => {
    switch (action.type) {
        case D.CHANGE_PARAMS :
// console.log('case');
            return {
                ...state, ...action.params
            }
        default:
            return state
    }
}

export const R_FU_USERS_FIND_CHANGE=(params)=>({type:D.CHANGE_PARAMS,params})