import React, { useEffect, useMemo, useState } from "react";
import C from './Mesager.module.css'

import Chat from "./Chat";
import Friend_panel from "./Friends_panel";

const Mesager = (props) => {
   
    let touchstart
    let ref_men=null
    let ref_close=null
    let left
    const  setMenRef = element => {
        ref_men = element;
    }
    const  setCloseRef = element => {
        ref_close = element;
    }
   
    const touch_start_cont=(e)=>{
        touchstart = e.changedTouches[0].clientX
        left=ref_men.clientWidth

    }

    const touch_move_cont=(e)=>{
        ;
        if(touchstart<50){
            ref_men.style.transform = `translate(${-(left-(e.changedTouches[0].clientX-touchstart))+'px'},0)`
            
            
        }
    }
    const touch_end_cont=(e)=>{
        if(touchstart<50){
            if((document.body.offsetWidth/2)<e.changedTouches[0].clientX){
                const l = document.body.offsetWidth - ref_men.offsetWidth
                ref_men.style.transform=`translate(${l}px,0)`
                
                ref_close.classList.add(C.active)
            }
            else if (e.changedTouches[0].clientX>=0){
                ref_men.style.transform=`translate(0,0)`
                ref_close.classList.add(C.active)
        }
        }
    }
    const CloseDoubleClick=(e)=>{
        
        ref_close.classList.remove(C.active)
        ref_men.style.transform=`translate(${-left}px,0)`
    }
    
    



    return <div className={C.mesager_container}  >
        <Friend_panel {...props} ref_men={setMenRef} />
        <Chat match={props.match} setNew={props.setNew} onTouchEnd={touch_end_cont} onTouchMove={touch_move_cont} onTouchStart={touch_start_cont} text={props.text}/>
        <div className={C.close_swipe} ref={setCloseRef} onClick={CloseDoubleClick} ></div>
    </div>
}




export default Mesager